.about {
	padding-top: 55px;
	padding-bottom: 112px;
	@include adaptive(1023) {
		padding-bottom: 92px;
	}
	@include adaptive(767) {
		padding-top: 30px;
		padding-bottom: 50px;
	}

	&__wrapper {
		//display: flex;
		//align-items: flex-start;
		//@include adaptive(1023) {
		//	flex-wrap: wrap;
		//}

		&-left {
			//flex: 1;
			//min-width: 1px;
			//max-width: 100%;
			//padding-right: 74px;
			//@include adaptive(1365) {
			//	padding-right: 54px;
			//}
			//@include adaptive(1279) {
			//	padding-right: 34px;
			//}
			//@include adaptive(1023) {
			//	padding-right: 0;
			//}
		}

		&-right {
			//width: 100%;
			//max-width: 351px;
			//@include adaptive(1023) {
			//	max-width: 100%;
			//}
		}

		&-right {
			padding-top: 93px;
			@include adaptive(1279) {
				padding-top: 50px;
			}
			@include adaptive(1023) {
				padding-top: 30px;
			}
		}
	}

	&__title {
		padding-bottom: 42px;
		color: #00000b;
		font-family: 'Merriweather', sans-serif;
		font-size: 36px;
		@include adaptive(1365) {
			font-size: 33px;
		}
		@include adaptive(767) {
			padding-bottom: 40px;
			font-size: 28px;
		}
	}

	&__subtitle {
		padding-bottom: 17px;
		color: #00000b;
		font-family: 'Merriweather', sans-serif;
		font-size: 28px;
		@include adaptive(1023) {
			font-size: 26px;
		}
		@include adaptive(767) {
			font-size: 20px;
		}
	}

	&__desc {
		padding-bottom: 42px;
		color: #252525;
		font-size: 15px;
		font-weight: 300;
		line-height: 1.6;
	}

	&__line {
		position: relative;
		width: 100%;
		height: 1px;
		margin-bottom: 38px;
		background-color: #d0cece;

		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 50%;
			height: 2px;
			background-color: #412d12;
			@include transition(width);
		}
	}

	&__details {
		img {
			float: left;
			width: 220px;
			margin-right: 30px;
			margin-bottom: 30px;
			@include adaptive(1279) {
				width: 190px;
				margin-bottom: 20px;
			}
			@include adaptive(767) {
				float: none;
				display: block;
				max-width: 220px;
				margin-left: auto;
				margin-right: auto;
				height: auto;
				max-height: 100%;
			}
		}

		&-text {
			display: inline;
			@include adaptive(767) {
				display: block;
			}

			p {
				padding-bottom: 20px;
				color: #252525;
				font-weight: 300;
				font-size: 17px;
				line-height: 1.6;

				strong {
					font-weight: 400;
				}
			}
		}
	}

	&__img {
		overflow: hidden;
		position: relative;
		width: 100%;
		height: 344px;
		margin-bottom: 24px;
		@include adaptive(767) {
			height: 211px;
		}

		img {
			@extend %imageResCoverAbs;
		}
	}

	&__desc {
		padding-bottom: 29px;
		font-weight: 300;
		font-size: 17px;
		color: #252525;
	}

	&__form {
		display: flex;
		flex-direction: column;
		font-family: 'Merriweather', sans-serif;

		&-field {
			position: relative;
			padding-bottom: 10px;

			&.is-focus {
				input,
				textarea {
					background-color: transparent;
					@include transition(background-color);
				}

				.about__form-label {
					transform: scale(0.7) translateY(-40%);
					@include transition(transform);
				}
			}

			input,
			textarea {
				outline: none;
				position: relative;
				width: 100%;
				box-shadow: none;
				border-style: solid;
				border-width: 1px;
				border-color: rgb(208, 206, 206);
				border-radius: 4px;
				background-color: rgb(237, 233, 224);
				@include transition(background-color);

				&:focus {
					background-color: transparent;
					@include transition(background-color);
				}
			}

			input {
				height: 56px;
				padding: 0 20px;
				@include adaptive(767) {
					height: 46px;
				}
			}

			textarea {
				resize: none;
				height: 104px;
				padding: 20px;
				@include adaptive(767) {
					height: 100px;
				}
			}
		}

		&-label {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 56px;
			padding: 0 20px;
			line-height: 56px;
			font-size: 13px;
			font-weight: 300;
			color: #827a70;
			transform-origin: 20px center;
			backface-visibility: hidden;
			@include transition(transform);
			@include adaptive(767) {
				height: 52px;
			}
		}

		&-btn {
			cursor: pointer;
			outline: none;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 52px;
			border-radius: 4px;
			border: none;
			box-shadow: none;
			background-color: rgb(65, 45, 18);
			@include transition(background-color);

			&:hover {
				background-color: #00000b;
				@include transition(background-color);
			}

			span {
				font-size: 22px;
				color: rgb(255, 255, 255);
				line-height: 1.545;
				@include adaptive(1023) {
					font-size: 20px;
				}
				@include adaptive(767) {
					font-size: 18px;
				}
			}
		}
	}
}

// SECTION :: END
